import React, { useState, useEffect, useRef, useCallback } from "react";
import AddIcon from '@mui/icons-material/Add';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import styles from '@/views/partials/card.module.css';
import clsx from 'clsx';


function DisplayCard(props: any) {
    const { id, task, description } = props; // Destructure props for clarity
    
    // State to track the touch position and whether the card is currently being dragged
    const [dragging, setDragging] = useState(false);
    const [offset, setOffset] = useState({ x: 0, y: 0 }); // Stores current transform offset
    const cardRef = useRef<HTMLDivElement>(null);

    function editForm() {
        props.onEdit(props);
    }
    
    function deleteForm() {
        props.onDelete(props.id);
    }

    const handleDragStart = (e: any) => {
        // CRITICAL: Store the task ID so the drop target knows which task to move
        e.dataTransfer.setData("taskId", props.id.toString()); 
    };

    // --- TOUCH DRAG HANDLERS ---
    
    // 1. Starts the drag
    const handleTouchStart = (e: any) => {
        e.stopPropagation(); 
        
        sessionStorage.setItem("draggingTaskId", props.id.toString());
        
        const touch = e.touches[0];
        const rect = e.currentTarget.getBoundingClientRect();
        
        setDragging(true);
        setOffset({
            x: touch.clientX - rect.left,
            y: touch.clientY - rect.top,
        });

        // Add visual feedback
        e.currentTarget.style.opacity = '0.8';
        e.currentTarget.style.zIndex = '1000'; 
    };

    // 2. Handles movement (Attached via useEffect)
    const handleTouchMove = useCallback((e: any) => {
        if (!cardRef.current || !dragging) return;
        
        e.preventDefault(); 

        const touch = e.touches[0];
        const newX = touch.clientX - offset.x;
        const newY = touch.clientY - offset.y;

        cardRef.current.style.transform = `translate(${newX}px, ${newY}px)`;
    }, [dragging, offset]);

    // 3. Ends the drag
    const handleTouchEnd = (e: any) => {
        setDragging(false);
        sessionStorage.removeItem("draggingTaskId");

        const cardElement = e.currentTarget;

        // Reset visual state
        cardElement.style.opacity = '1';
        cardElement.style.zIndex = 'auto';
        cardElement.style.transform = 'none'; 
    };

    // --- EFFECT: Manual Attachment of touchmove Listener ---
    // This attaches the listener directly to the DOM with { passive: false }
    useEffect(() => {
        const element = cardRef.current;
        // TypeScript now knows 'element' is an HTMLDivElement
        if (element) {
            // Attach the listener explicitly using addEventListener
            element.addEventListener('touchmove', handleTouchMove, { passive: false });
        }
        
        // Cleanup function to remove the listener when the component unmounts
        return () => {
            if (element) {
                element.removeEventListener('touchmove', handleTouchMove);
            }
        };
    }, [handleTouchMove]); // Re-attach only if handleTouchMove changes

    
    return (
        <div 
            ref={cardRef} // ðŸ’¡ FIX 1: Attach the ref to the outer div
            className={styles.cardParent} 
            draggable='true' 
            onDragStart={handleDragStart} 
            onTouchStart={handleTouchStart} 
            // ðŸ’¡ FIX 2: onTouchMove REMOVED from JSX. It is attached via useEffect.
            onTouchEnd={handleTouchEnd}
            
            // ðŸ’¡ FIX 3: Apply temporary positioning style for visual dragging
            style={dragging ? { position: 'fixed', top: 0, left: 0, width: cardRef.current ? cardRef.current.offsetWidth : '300px', height: cardRef.current ? cardRef.current.offsetHeight : '300px'} : {height: "300px"}}
        >
            <div className={styles.card}>
                <h3>{props.task}</h3>
                <p>{props.description}</p>
                <div className={styles.bottomSection}>
                    <p>Due: {props.due_date && new Date(props.due_date).toDateString() || 'N/A'}</p>
                    <p>Priority: {props.priority}</p>
                </div>
                <div className={styles.icons}>
                    <button className={clsx(styles.edit, styles['my-button'])} onClick={editForm}>
                        <EditIcon style={{ color: 'white' }} />
                    </button>
                    <button className={clsx(styles.delete, styles['my-button'])} onClick={deleteForm}>
                        <DeleteIcon style={{ color: 'white' }} />
                    </button>
                </div>
            </div>
        </div>
    );

}

export default DisplayCard;